export default {
    data(){
        return{}
    },
    watch:{
        GraphsData(curVal,oldVal){
            this.drawGraphs()
        }
    },
    mounted(){
        this.drawGraphs()
    },
    methods:{
        drawGraphs(){
            let that = this
            let Graphs = this.$echarts.init(this.$refs.echarts)
            Graphs.setOption({
                color:['rgba(254, 153, 66, 1)','rgba(64, 137, 255, 1)'],
                tooltip: {
                    trigger: 'axis',
                    formatter:function(params, ticket, callback){
                        var htmlStr = '';
                        for(var i=0;i<params.length;i++){
                            if(i===0){
                                htmlStr += params[i].name + '<br/>'//x轴的名称
                            }
                            htmlStr += params[i].marker
                            htmlStr += params[i].seriesName + ':'
                            htmlStr += params[i].value + '%' + '<br/>'
                        }
                        return htmlStr
                    }
                },
                legend: {
                    icon:'rect',
                    itemWidth: 10,
                    itemHeight: 10,
                    itemGap: 10,
                    x:'center',
                    y:'bottom'
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: true,
                    data: ['111','222','333','444','555'],
                    splitLine:{show: false},    //去除网格线
                    axisLine: {
                        lineStyle: {
                            type: 'solid',
                            color: 'rgba(224, 224, 224, 1)',   //左边线的颜色
                            width:'1'           //坐标线的宽度
                        }
                    },
                    axisLabel: {textStyle: {color: '#000'}} //坐标值的具体颜色
                },
                yAxis: {
                    type: 'value',
                    gridIndex: 0,
                    splitLine:{
                        lineStyle:{
                            color:'rgba(224, 224, 224, 1)'
                        }
                    },
                    axisTick:{show:false},
                    axisLine: {
                        show:false
                    },
                    axisLabel: {
                        padding:[0,0,5,0],
                        margin:0,
                        fontSize:10,
                        inside:true,
                        textStyle: {color: 'rgba(224, 224, 224, 1)'},
                        formatter: '{value} %' 
                    }
                },
                series: [
                    {
                        name:'no1',
                        type:'line',
                        smooth: true, // 折线变曲线
                        symbol:false, // 无拐点样式
                        symbolSize:0, // 拐点大小为0
                        // stack: '总量',
                        data:[20, 32, 10, 34, 90]
                    },
                    {
                        name:'no2',
                        type:'line',  
                        symbol:false, // 无拐点样式
                        symbolSize:0, // 拐点大小为0
                        smooth: true, // 折线变曲线
                        // stack: '总量',
                        data:[20, 18, 91, 34, 90]
                    }
                ]
            })
            window.addEventListener("resize", Graphs.resize) // echarts提供自动缩放
        }
    }
}



